.hero-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 15px;
    gap: 15px;
    justify-content: center;
    text-align: center;
    height: 100vh;
    background-color: #f2f2f2;
    padding-top: 2vh;
  }

  .hero-image img {
    width: 100%;
    min-width: 500px;
    max-width: 800px;
    height: auto;
  }

  .hero-text h2 {
    font-size: 2.5rem;
    margin-top: 20px;
    color: #333;
  }
  .hero-text button {
    padding: 10px 20px;
    font-size: 1.2rem;
    color: #fff;
    background-color: #764ED2;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
    animation: pulse 1s infinite alternate;
  }

  .hero-text button:hover {
    animation: shake 0.5s;
    animation-fill-mode: forwards;
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.10);
    }
  }

  @keyframes shake {
    0% {
      transform: translateX(0);
    }
    25% {
      transform: translateX(-5px);
    }
    50% {
      transform: translateX(5px);
    }
    75% {
      transform: translateX(-5px);
    }
    100% {
      transform: translateX(0);
    }
  }
  .hero-text p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 10px;
  }

  @media (max-width: 1068px) {
    /* Styles for screen width up to 1068px */
    .hero-text {
      padding-bottom: 150px;
    }
    .hero-text h2 {
      font-size: 2.2rem;
    }

  }
  @media (max-width: 768px) {
    /* Styles for screen width up to 768px */
    .hero-text h2 {
      font-size: 2rem;
    }

  }