.about-container {
    min-width: 77vw;
    margin: 0 auto;
    padding: 50px;
    background-color: #764ED2;
    text-align: center;
    overflow-x: hidden;
    padding-bottom: 3vh;
  }
  
  .about-container h2 {
    font-size: 32px;
    color: white;
    margin-bottom: 20px;
    
  }
  
  .about-container p {
    font-size: 18px;
    color: white;
    line-height: 1.5;    
    text-align: center;
  }
  
  .about-container button {
    background-color: transparent;
    color: #fff;
    border: 1px solid white;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
.about-container button:hover {
    background-color: white;
    color: black;
    transition: background-color 0.3s ease;
  }
  
.about-container button:focus {
    outline: none;
  }