nav {
    background-color: transparent;
    padding: 10px;
    position: fixed;
    top: 0;
    right: 0;

  }
  
  .nav-link {
    float: right;
    color: black;
    text-decoration: none;
    padding: 10px;
    margin-right: 10px;
    position: relative; /* Add position relative */
  }
  
  .nav-link:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 0;
    background-color: #764ED2;
    transition: width 0.5s ease; /* Transition the width property */
  }
  
  .nav-link:hover:before {
    width: 100%; /* Increase width to 100% on hover */
  }
  
  .nav-link:hover {
    color: #764ED2;
  }

  .nav-link.active {
    color: #764ED2;
  }
  